$color-darkBlue-texts: #11103C;
$color-yellow-texts: #DBA000;
$color-black: #000;
$color-white: #fff;
$color-grey-rodape: rgb(235, 235, 235);

@media( max-width: 700px) {

    .header-desktop {
        display: none;
    }
    
    .header-mobile {
        width: 100vw;
        z-index: 3;
        text-transform: uppercase;
        position: fixed;
        padding-left: 2rem;
        justify-content: space-between;
        height: 90px;
        display: flex;
        background-size: cover;
        background-image: url(../../images-kairos/bg-header.png);
        align-items: center;

        .btn-menu {
            z-index: 2;
            width: 38px;
            height: 38px;
            transition: .4s;
            text-align: center;
            position: absolute;
            border-radius: 16px;
            background-color: $color-yellow-texts;

            .menuHamburguer {
                width: 100%;
                height: 100%;
                background-size: 90%;
                background-position: center;
                background-repeat: no-repeat;
                background-image: url(../icones-menu-hamburguer/icone-menu-hamburguer.png);
            }

            .fecharMenu {
                font-size: 1.5rem;
                font-weight: lighter;
                line-height: 2.4rem;
            }

        }


        h1 {
            text-shadow:  1px 1px $color-black;
            font-size: 2rem;
            letter-spacing: 3px;
            color: $color-yellow-texts;
        }

        .navegacao-mob {
            width: 100%;
            transition: .3s;
            position: relative;
            
            ul {
                padding-top: 8vh;
                padding-bottom: 2vh;
                list-style-type: none;
                justify-content: center;
                max-height: 100vh;
                flex-direction: column;
                display: flex;
                border-radius: 0 0 0 5px;
                background-color: $color-darkBlue-texts;
                
                li {

                    a {
                        text-decoration: none;
                        padding: 1.2rem 2rem;
                        font-size: 4vw;
                        display: block;
                        color: $color-white;
                    }
                }

                li.servicos {
                    position: relative;

                    .servicos-menu {
                        padding-top: 1vh;
                        border-radius: 0 0 0 1rem;
                        background-color: $color-white;
    
                        li a {
                            padding: .8rem;
                            padding-left: 2.3rem;
                            position: relative;
                            font-weight: bold;
                            font-size: 3.5vw;
                            flex-direction: column;
                            color: $color-darkBlue-texts;
    
                            &:active {
                                background-color: #11103C;
                            }

                        }
                    }
                }

            }
        }
    }

    .section-home {
        width: 100vw;
        padding: 0;
        background-size: cover;
        background-position: top;
        background-image: url(../../images-kairos/bg-home-mobile.jpg);

        .conteudo-container {
            width: 100%;
            text-align: center;
            padding: 0;
            padding-top: 1rem;
            gap: 2rem;
            backdrop-filter: blur(1px);

            .logo-text {
                gap: .5rem;

                h1 {
                    text-shadow: -4px 2px $color-darkBlue-texts;
                    text-align: justify;
                    font-size: 9vw;

                }

                h2 {
                    text-shadow: 1px 1px $color-black;
                    letter-spacing: 1px;
                    margin-top: .5rem;
                    font-size: 1.2rem;
                    box-shadow: none;
                }
            }

            .saiba-mais {
                
                a {

                    &::after {
                        width: 30px;
                        top: 3.2rem;
                        height: 30px;
                    }
                }
            }
        }
    }

    .background-conteudo {
        gap: 2rem;
    }

    .circle-title-section {
        font-size: 6vw;
    }

    .section-quem-somos {
        margin-top: -1rem;

        .container-quem-somos {
            min-width: 85%;

            .parte-texto {
                min-width: 100%;
                
                p {
                    font-size: .85rem;
                }
            }
        }
    }

    .section-oque-fazemos {
        .container-oque-fazemos {
            width: 85%;
            padding: 1rem;
            gap: .2rem;
            flex-direction: column;
            align-items: center;
            
            div {
                width: 100%;
                opacity: 1;
                height: 100px;

                p {
                    font-size: 1rem;
                }
            }
        }
    }

    .section-servicos {
        .container-servicos {
            width: 80%;
            padding: 1rem;

            h1 {
                margin: 1.5rem 0 .5rem 0;
                font-size: 5.5vw;
            }

            .images {
                overflow-y: scroll;
                height: 500px;
                grid-template-columns: 1fr;

                div {
                    width: 100%;
                    height: 200px;
                    display: inline-block;
                }
            }
        }
    }

    .rodape {
        .container-contato {

            .parte-texto {
                margin-left: 0;
                align-items: center;

                h2 {
                    font-size: 1.5rem;
                }

                .descricao-contato {
                    width: 80%;
                    text-align: justify;
                }

                .texto-whats-email {
                    margin-top: 2rem;

                    .ant-anchor {
                        gap: 1.5rem;

                        .gmail {
                            .aviso-gmail-copiado {
                                margin-left: -3rem;
                            }
                        }

                        .facebook, .whatsapp {
                            width: 45px;
                            height: 45px;
                        } .instagram {
                            width: 47px;
                            height: 47px;
                        } .gmail {
                            width: 42px;
                            height: 42px;
                        }
                    }
                }

                .infos {
                    width: 100%;
                    text-align: center;
                    flex-direction: column;
                }
            }
        }
    }
    
}

@media(max-width: 540px){
    .aviso-gmail-copiado {
        margin-top: .5rem;
        margin-left: -8rem !important;
    }
}