$color-darkBlue-texts: #11103C;
$color-yellow-texts: #f0b000;
$color-black: #000;
$color-white: #fff;
$color-grey-rodape: rgb(235, 235, 235);

.header-mobile {
    display: none;
}

.header-desktop {
    z-index: 1;
    width: 100%;
    text-transform: uppercase;
    position: fixed;
    padding-inline: 8vw;
    justify-content: space-between;
    height: 80px;
    display: flex;
    border-radius: 1px;
    background-size: 60%;
    background-position: center;
    background-image: url(../../images-kairos/bg-header.png);
    align-items: center;

    h1 {
        text-shadow:  1px 1px $color-black;
        font-size: 1.8rem;
        letter-spacing: 3px;
        color: $color-yellow-texts;
    }

    .navegacao {

        ul {
            z-index: 1;
            position: relative;
            gap: 0;
            display: flex;
            list-style-type: none;

            li {

                a {
                    text-decoration: none;
                    padding: .7rem 1rem;
                    font-size: 1rem;
                    cursor: pointer;
                    color: $color-white;

                    &:hover {
                        transition: .3s;
                        background-color: rgba(255, 255, 255, .2);
                    }
                }
            }

            .servicos {
                position: relative;

                ul { 
                    z-index: -1;
                    width: 150px;
                    top: 1.5rem;
                    text-align: center;
                    right: -1.7vw;
                    position: absolute;
                    height: 320px;
                    gap: 1rem;
                    flex-direction: column;
                    display: none;
                    background-color: #1c2253;
    
                    li {
                        position: relative;
                        padding: .5rem .2rem;
                        cursor: pointer;
                        color: $color-white;
                        bottom: -20px;
    
                        a {
                            font-weight: lighter;
                            font-size: 1rem;
                        }
                    }
                }
            }
            
            .servicos:hover ul {
                display: flex;
            }
            
                
        }
    }
}


.section-home {
    width: 100%;
    justify-content: center;
    height: 100vh;
    display: flex;
    background-size: cover;
    background-position: center;
    background-repeat: no-repeat;
    background-image: url(../../images-kairos/imagem-container-home.jpg);
    align-items: center;

    .conteudo-container {
        padding: 2rem;
        height: 100%;
        gap: 3rem;
        justify-content: center;
        flex-direction: column;
        display: flex;
        cursor: default;
        border-radius: 5px;
        backdrop-filter: blur(1px);

        .logo-text {
            padding: .5rem;
            flex-direction: column;
            display: flex;
            align-items: center;

            h1 {
                text-shadow: -3px 2px $color-black;
                text-transform: uppercase;
                font-weight: bold;
                font-size: 4vw;
                color: $color-yellow-texts;
            }

            h2 {
                text-shadow: 1px 1px $color-darkBlue-texts;
                padding: 4px 8px;
                margin-top: 8px;
                margin-right: -22px;
                font-size: 2vw;
                color: $color-yellow-texts;
                box-shadow: inset 0px 0px 2px $color-black;
                border-radius: 5px;
                background-color: $color-white;
            }
        }

        .saiba-mais {
            text-align: center;
            cursor: pointer;

            a {
                text-decoration: none;
                position: relative;
                padding: 1.5rem 1rem 3.5rem 1rem;
                font-size: 1.3rem;
                color: $color-yellow-texts;

                &:hover {
                    transition: .3s;
                    background-color: rgba(250, 250, 250, .3);
                }

                &::after {
                    width: 40px;
                    top: 60px;
                    position: absolute;
                    left: 49px;
                    height: 40px;
                    content: '';
                    background-size: contain;
                    background-repeat: none;
                    background-image: url(../../images-kairos/icone-seta-home-pg.png);
                }
            }

            p {
                font-size: 1.4rem;
                color: $color-yellow-texts;
            }

        }
    }
}

.background-conteudo {
    gap: 4rem;
    flex-direction: column;
    display: flex;
    background-size: 100%;
    background-position: bottom;
    background-repeat: repeat-y;
    background-image: url(../../images-kairos/bg-site-repeat.jpg);
}

.circle-title-section {
    text-transform: uppercase;
    text-shadow: 2px 2px $color-white;
    margin-bottom: 12px;
    font-size: 2rem;
    display: inline-block;
    color: $color-darkBlue-texts;

    &::before {
        content: '';
        width: 10px;
        position: absolute;
        margin: 10px -20px;
        height: 10px;
        border-radius: 10px;
        border: 2px solid $color-white;
        background-color: $color-yellow-texts;
    }
}


.section-quem-somos {
    width: 100%;
    margin-top: -2.5rem;
    text-align: center;

    .container-quem-somos {
        width: 75%;
        margin: 0 auto;
        height: 100%;
        grid-template-columns: 55% 45%;
        display: grid;
        box-shadow: 0 0 5px $color-black;
        background-color: $color-white;

        .parte-texto {
            padding: 3rem 4rem;
            justify-content: center;
            gap: 1rem;
            flex-direction: column;
            display: flex;

            p {
                text-align: justify;
                line-height: 25px;
                font-size: .95rem;
            }

            .infos {
                width: 100%;
                padding: 1rem .5rem;
                justify-content: center;
                gap: 3rem;
                display: flex;
                color: $color-white;
                border-radius: 4px;
                background-color: $color-darkBlue-texts;
                align-items: center;


                p {
                    width: 35%;
                    text-align: center;
                    position: relative;
                    font-size: .9rem;
                }
            }
        }

        .parte-logo {
            justify-content: center;
            display: flex;
            background-color: $color-darkBlue-texts;
            align-items: center;

            .logo {
                width: 100%;
                margin-bottom: 10px;
                height: 250px;
                background-size: contain;
                background-repeat: no-repeat;
                background-position: center;
                background-image: url(../../images-kairos/logo-grande-fundo-transparente.png);

                p {
                    position: relative;
                    padding: 0;
                    color: $color-yellow-texts;
                    bottom: -215px;
                }

                p:nth-child(2) {
                    margin-top: -5px;
                }
                p:nth-child(3) {
                    margin-top: -5px;
                }
            }
        }
    }
}

.section-oque-fazemos {
    width: 100%;
    justify-content: center;
    flex-direction: column;
    display: flex;
    align-items: center;

    .container-oque-fazemos {
        width: 75%;
        justify-items: center;
        height: 100%;
        display: flex;
        align-content: center;
        box-shadow: 0 0 5px $color-darkBlue-texts;
        background-color: $color-white;

        .construcao {
            background-image: url(../../images-kairos/img-construcao.png);
        }
        .reforma {
            background-position: center;
            background-image: url(../../images-kairos/img-reformas.png);
        }
        .acabamento {
            background-image: url(../../images-kairos/img-acabamentos.png);
        }
        .pintura {
            background-image: url(../../images-kairos/img-pintura.png);
        }
        .hidraulica {
            background-image: url(../../images-kairos/img-hidraulica.png);
        }
        .eletrica {
            background-image: url(../../images-kairos/img-eletrica.png);
        }
    
        div {
            width: 250px;
            opacity: 1;
            justify-content: center;
            height: 350px;
            display: flex;
            background-size: cover;
            background-repeat: no-repeat;
            background-position: center;
            align-items: center;
            
            &:hover {
                transition: .5s;
                transform: scale(1.05);
            }
    
            p {
                text-transform: uppercase;
                text-shadow: 1px 1px $color-white;
                font-weight: bold;
                font-size: 1.4vw;
            }
        }
    }

}

.section-servicos {
    width: 100%;
    justify-content: center;
    flex-direction: column;
    display: flex;
    align-items: center;

    .container-servicos {
        width: 75%;
        text-align: center;
        padding: 1rem 2rem;
        background-color: $color-white;
            
        h1 {
            padding: .2rem;
            margin-top: 2.5rem;
            margin-inline: .5rem;
            margin-bottom: .5rem;
            font-weight: 300;
            font-size: 1.8vw;
            color: $color-white;
            border-radius: 3px;
            background-color: $color-darkBlue-texts;
        }

        h1:nth-child(1){
            margin-top: 0;
        }

        .images {
            padding: .5rem;
            grid-template-columns: 1fr 1fr 1fr;
            gap: .5rem;
            display: grid;

            div {
                width: 100%;
                height: 250px;
                background-size: cover;
                background-repeat: no-repeat;

                &:hover {
                    transform: scale(1.4);
                    transition: 1s;
                }

            }
        }
    }
}


.rodape {
    padding: 2rem 1rem;
    height: 100%;
    background-color: #1c2b53;

    .container-contato {
        overflow: hidden;
        display: flex;

        .parte-texto {
            width: 100%;
            padding: 2rem 0;
            gap: 1rem;
            font-weight: 500;
            flex-direction: column;
            display: flex;
            color: $color-white;
            align-items: center;

            h2 {
                text-shadow: -1px 1px $color-black;
                letter-spacing: 1px;
                font-size: 1.6rem;
                color: $color-grey-rodape;
                align-self: left;
        
                &::before {
                    background: none;
                    border: none;
                }
            }

            .descricao-contato {
                width: 70%;
                text-align: center;

                span {
                    font-weight: bold;
                    color: $color-yellow-texts;
                }
            }


            .texto-whats-email {
                width: 48%;
                margin-top: 1rem;
                justify-content: center;
                display: flex;

                .ant-anchor {
                    position: relative;
                    gap: 1rem;
                    display: flex;

                    a, p {
                        padding:  .8rem 1rem;
                        cursor: pointer;
                    }

                    .facebook, .whatsapp, .instagram, .gmail {
                        background-size: cover;
                        background-repeat: no-repeat;

                        &:hover {
                            transition: .3s;
                            transform: scale(1.1);
                        }
                    }
    
                    .facebook {
                        width: 35px;
                        height: 35px;
                        background-image: url(../../images-kairos/icone-facebook.png);
                    }
        
                    .whatsapp {
                        width: 35px;
                        padding: .2rem;
                        height: 35px;
                        background-image: url(../../images-kairos/icone-whatsapp.png);
                    }   

                    .instagram {
                        width: 37px;
                        height: 36px;
                        background-image: url(../../images-kairos/icone-instagram.png);
                    }   
        
                    .gmail {
                        width: 33px;
                        margin-top: .1rem;
                        height: 33px;
                        background-image: url(../../images-kairos/icone-gmail.png);

                        .aviso-gmail-copiado {
                            width: 300px;
                            top: 1.5rem;
                            position: absolute;
                            margin-left: -2rem;
                            opacity: 0;
                            font-weight: 300;
                            color: #ff5c5c;
                            animation-duration: 1.5s;
                            animation-fill-mode: backwards;
    
                            @keyframes aviso-animation {
                                from {
                                    opacity: 0;
                                } to {
                                    opacity: 1;
                                }
                            }
                        }
                    }

                }   
            }  
            
            .infos {
                width: 50%;
                text-align: center;
                margin-top: 2rem;
                gap: 2rem;
                flex-direction: column;
                display: flex;

                p {
                    padding: .1rem 0;
                    letter-spacing: .4px;
                    font-weight: 300;
                    font-size: .8rem;
                    color: rgba(250, 250, 250, .9);
                }

                p:nth-child(1) {
                    font-weight: 500;
                }

                p:nth-child(5) {
                    margin-top: 1rem;
                }

                a {
                    text-decoration: none;
                    font-weight: 300;
                    font-size: .8rem;
                    color: rgba(250, 250, 250, .9);
                }
            }
        }

        .parte-logo {
            width: 80%;
            justify-content: center;
            flex-direction: column;
            display: flex;
            align-items: center;

            .logo {
                width: 250px;
                text-align: center;
                height: 250px;
                background-size: contain;
                background-repeat: no-repeat;
                background-position: center;
                background-image: url(../../images-kairos/logo-grande-fundo-transparente.png);

                p {
                    position: relative;
                    padding: 0;
                    color: $color-yellow-texts;
                    bottom: -215px;
                }

                p:nth-child(2) {
                    margin-top: -5px;
                }
                p:nth-child(3) {
                    margin-top: -5px;
                }
            }

            h3 {
                margin-top: 3rem;
                font-weight: 500;
                font-size: 1rem;
                color: $color-white;
            }
        }
    }  
}


@media(max-width: 900px) {
    .header-desktop {
        padding-inline: 4vw;

        h1 {
            font-size: 1.5rem;
        }

        .navegacao {
            ul {
                li {
                    a {
                        font-size: .9rem;
                    }
                }

                .servicos {
                    ul {
                        right: -3.2vw;
                        li {
                            a {
                                font-size: .9rem;
                            }
                        }
                    }
                }
            }
        }
    }

    .section-quem-somos {
        .container-quem-somos {
            grid-template-columns: 1fr;

            .parte-logo {
                height: 320px;
            }
        }        
    }

    .section-servicos {
        .container-servicos {
            h1 {
                font-size: 1.5rem;
            }

            .images {
                div {
                    height: 200px;
                }
            }
        }
    }

    .section-contato {
        .container-contato {
            .parte-texto {
                .texto-whats-email {
                    p {
                        letter-spacing: 0px;
                        font-weight: 500;
                        font-size: 1.6vw;
                    }

                    p:nth-child(1) {

                        &::before {
                            width: 25px;
                            height: 25px;
                        }
                    }
                
                    p:nth-child(2) {
                        &::before {
                            width: 20px;
                            height: 20px;
                        }
                    }
                }
            }
        }

    }

    .rodape {

        .container-contato {
            flex-direction: column;
            align-items: center;
        } 

        div {
            p {
                font-size: .9rem;
            }
        }
    }
}